@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-menu{
    background-image: url(../img/cafe.jpg);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 100%;
    aspect-ratio: 3/5;
    @include m.tablet { 
        aspect-ratio: 3/4;
    }
    @include m.desktop { 
        @include m.grid(2, v.$separacion);
        aspect-ratio: 16/6;
        background-position: bottom right;
        background-size: 40%;
    }
}

.texto{

    p{
        font-size: 2rem;
    }
}